* {
    margin: 0;
    padding: 0;
    list-style: none;
    text-decoration: none;
    outline: none;
    border: none;
    box-sizing: border-box;
    font-family: 'Inter', sans-serif;
  }
  
  img {
    max-width: 100%;
    display: block;
  }
  
  html {
    background-color: #F7F5F9;
    font-size: 62.5%;
  }
  
  html,
  body {
    overflow-x: hidden;
  }
  
  button {
    background-color: transparent;
    cursor: pointer;
  }
  