.hero {
  background-color: $orange;
  border-radius: 0 0 10rem 10rem;
  .container {
    width: 100%;
    padding-top: 5.5rem;
    .burger {
      display: flex;
      justify-content: flex-end;
      padding-right: 1.5rem;
      padding-bottom: 1.5rem;
    }
    .profile {
      display: flex;
      flex-direction: column;
      align-items: center;
      img {
        width: 16rem;
        border-radius: 5rem;
        padding-bottom: 1.8rem;
      }
      h1 {
        padding-bottom: 3.3rem;
      }
    }
    .social-group {
      display: flex;
      justify-content: space-between;
      padding-bottom: 2.3rem;
      .social {
        display: flex;
        align-items: center;
        img {
          width: 6.24rem;
        }
        .info {
          p {
            font-family: "Poppins", sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 16.9px;
            line-height: 25px;
            color: rgba(247, 245, 249, 0.7);
          }
        }
      }
      .info {
        padding-left: 2.3rem;
        display: flex;
        flex-direction: column;
        align-items: center;
        width: 100%;
      }
    }
    .parceria {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding-bottom: 4.4rem;
      h3 {
        font-family: "Inter", sans-serif;
        font-style: normal;
        font-weight: 500;
        font-size: 26px;
        line-height: 31px;
      }
      .contatos {
        display: flex;
        align-items: center;
        a {
          &:not(:last-child) {
            margin-right: 5rem;
          }
          
        }
        .parceria-block {
          img {
            padding-right: 1rem;
          }
          p {
            font-family: "Poppins", sans-serif;
            font-style: normal;
            font-weight: 400;
            font-size: 16.9px;
            line-height: 25px;
            &:hover{
                color: $white;
            }
          }
          padding-top: 1.7rem;
          display: flex;
          align-items: center;
        }
      }
    }
  }
  @include responsive(1240px) {
    border-radius: 0 0 5rem 5rem;
  }
  @include responsive(800px) {
    .container {
      .profile {
        h1 {
          font-size: 3.8rem;
          line-height: 5rem;
        padding-bottom: 1.5rem;
        }
      }
      .social-group {
        flex-wrap: wrap;
        // justify-content: center;
        justify-content: space-around;
        padding-bottom: 0;
        .social {
          width: 40%;
          justify-content: center;
          padding-bottom: 1.4rem;
        }
      }
      .parceria {
        .contatos {
          a {
            &:not(:last-child) {
              margin-right: 2rem;
            }
          }
          .parceria-block {
            p {
              font-size: 1.3rem;
            }
          }
        }
      }
    }
  }
  @include responsive(610px) {
    .container {
      .parceria {
        h3 {
          font-size: 20px;
          line-height: 24px;
        }
        padding-bottom: 2.4rem;
        .contatos {
          a {
            &:not(:last-child) {
              margin-right: 8rem;
            }
          }
        }
      }
      .parceria-block {
        p {
          display: none;
        }
      }
      .social-group {
        .social {
          justify-content: flex-start;
          img {
            width: 100%;
          }
        }
      }
    }
  }
  @include responsive(500px) {
    .container {
      .social-group {
        .social {
          .info {
            p {
              font-family: "Poppins", sans-serif;
              font-weight: 400;
              font-size: 13px;
              line-height: 20px;
            }
          }
        }
      }
    }
  }
  @include responsive(365px) {
    .container {
      .social-group {
        .social {
          width: 50%;
          .info {
            padding-left: 0;
          }
        }
      }
    }
  }
}

.mid {
  .container {
    h2 {
      padding: 3.8rem 0 3rem 0;
      font-size: 38px;
    }
    .cards {
      display: flex;
    //   flex-wrap: wrap;
      justify-content: space-between;
      align-items: center;
      width: 100%;
      .card {
        display: flex;
        flex-direction: column;
        padding-top: 2rem;
        align-items: center;
        width: 100%;
        max-width: 28rem;
        background-color: rgba(197, 190, 220, 0.1);
        // width: 100%;
        // height: 10.8rem;
        // height: 100% !important;
        // border-radius: 5%;
        // background-color: rgba(197, 190, 220, 0.1);
        // max-width: 400rem;
        // height: 32.3rem;
        // display: flex;
        // justify-content: flex-end;
        // align-items: center;
        // flex-direction: column;
        &:not(:last-child) {
          margin-right: 3rem;
        }
        img {
          width: 100%;
          max-width: 60%;
          color: orange;
        }
        .hidden {
          display: none;
        }
        transition: all 0.3s;
        &:hover {
          background-color: $orange;
          h2 {
            color: $secondary;
          }
          .hidden {
            display: flex;
          }
          .normal {
            display: none;
          }
        }
      }
    }
  }
  @include responsive(1120px) {
    .container {
      .cards {
        .card {
          // max-width: 20rem;
          h2 {
            font-size: 2.5rem;
          }
        }
      }
    }
  }
  @include responsive(880px) {
    .container {
        .cards {
            flex-wrap: wrap;
            justify-content: space-around;
          .card {
            min-width: 40%;
            &:not(:last-child){
                margin-right: 0;
            }
            margin-bottom: 3rem;
            h2 {
              font-size: 2.5rem;
            }
          }
        }
    }
  }
  @include responsive(612px) {
    .container {
        .cards {
          .card {
            min-width: 40%;
            max-width: 18rem;
            &:not(:last-child){
                margin-right: 0;
            }
            margin-bottom: 3rem;
            h2 {
              font-size: 2.5rem;
            }
          }
        }
    }
  }
  @include responsive(390px) {
    .container {
        .cards {
          .card {
            min-width: 40%;
            max-width: 16rem;
            &:not(:last-child){
                margin-right: 0;
            }
            margin-bottom: 3rem;
            h2 {
              font-size: 2rem;
            }
          }
        }
    }
  }
}
.footer {
    margin-top: 6rem;
    background-color: rgba(197, 190, 220, 0.1);
    text-align: center;
    padding: 20px;
    color: rgba(197, 190, 220, 0.1);
  
    p {
      margin: 0;
      color: $orange;
      a{
        color: blue;
      }
    }
  }
  
